<script>
export default {
  name: "ThongBao",
  data() {
    return {
      dataCategory: [],
      dataNotification: [],
      detailNotification: null,
      currentId: null,
      currentNotiId: 1,
    };
  },

  mounted() {
    this.$store.dispatch("ThongBao/layDanhMucThongBao").then((response) => {
      if (
        response.status === 200 &&
        response.data.data &&
        response.data.data.list
      ) {
        this.dataCategory = response.data.data.list;
        this.currentId = response.data.data.list[0].id;
        this.fn_GetDataNotification(response.data.data.list[0].id);
      }
    });
  },

  methods: {
    fn_ClickCategory: function (id) {
      this.currentId = id;
      this.fn_GetDataNotification(id);
    },

    addZero: function (num) {
      if (num < 10) {
        return `0${num}`;
      } else {
        return num;
      }
    },

    handleClickAtt: function (att) {
      window.open(att.url, "_blank");
    },

    fn_GetDataNotification: function (id) {
      this.$store
        .dispatch("ThongBao/layDanhSachThongBao", id)
        .then((lstNotification) => {
          if (
            lstNotification.status === 200 &&
            lstNotification.data.data &&
            lstNotification.data.data.list
          ) {
            let data = lstNotification.data.data.list;

            data.forEach((item) => {
              let _date = new Date(item.date_time);
              item.date_time_tmp = `${this.addZero(
                _date.getDate()
              )}/${this.addZero(_date.getMonth() + 1)}/${_date.getFullYear()}`;
            });

            this.dataNotification = data;

            this.fn_ClickNotification(data[0]);
          }
        });
    },

    fn_ClickNotification: function (detail) {
      if (detail) {
        this.currentNotiId = detail.id;

        let _date = new Date(detail.date_time);

        this.detailNotification = {
          title: detail.title,
          desc: detail.desc,
          date_time_tmp: detail.date_time_tmp,
          date_time_hour: `${this.addZero(_date.getHours())}:${this.addZero(
            _date.getMinutes()
          )}`,
          attachments: detail.attachments,
        };
      }
    },
  },
};
</script>

